<template>
  <div>
  <h2 class="name">
    {{name}}
  </h2>
  <ul>
    <group-item
      v-for=" (value, key, index) in group.items"
      :key="key"
      :value="value"
      :dbKey="key"
      :name="name"
      :index="index"
      :divider="group.divider"
    >
    </group-item>
  </ul>
  </div>
</template>

<script>
import GroupItem from '@/components/Item.vue';

export default {
  components: {
    GroupItem,
  },
  name: 'group',
  computed: {
    name() {
      return this.$route.params.name;
    },
    group() {
      return this.$store.getters.group(this.name);
    },
  },
};
</script>
