<template>
  <li
    :class="{
      'list-item': true,
      border: hasDivider,
      disabled: value.x,
      success: value.o
    }"
  >
    <span class="id">
      {{value.id}}
    </span>
    <button
      class="x button button--circle"
      v-if="!value.x"
      @click="disable()"
    >
      X
    </button>
    <button
      class="o button button--circle"
      v-if="!value.o && !value.x"
      @click="success()"
    >
      O
    </button>
    <button
      class="n button button--circle"
      v-if="!value.o && !value.x"
      @click="timestamp()">
      N
    </button>
    <div
      v-if="!value.x && !value.o"
      class="box"
    >
      <span
        v-for="(n, index) of value.n"
        :key="index"
      >
      </span>
    </div>
    <label v-if="!value.x" class="selector">
      <input v-model="selector" type="checkbox">
      <span class="label"></span>
      x
    </label>
  </li>
</template>

<script>

export default {
  props: [
    'value',
    'dbKey',
    'name',
    'index',
    'divider',
  ],
  computed: {
    hasDivider() {
      return (this.index + 1) % this.divider === 0;
    },
  },
  data() {
    return {
      selector: false,
    };
  },
  methods: {
    success() {
      this.selector = false;
      this.$store.dispatch('update', {
        name: this.name,
        key: this.dbKey,
        data: { o: true },
      });
    },
    disable() {
      this.selector = false;
      this.$store.dispatch('update', {
        name: this.name,
        key: this.dbKey,
        data: { x: true },
      });
    },
    timestamp() {
      this.selector = false;
      this.$store.dispatch('updateTime', {
        name: this.name,
        key: this.dbKey,
        data: Date.now(),
      });
    },
  },
};
</script>
